<template>
    <div class="basis" id="basis"> 
        <div class="base-head">
            <p>首页设置</p>
        </div>
        <div class="form">
            <el-form ref="form" :model="form" label-width="120px">
                <div class="index-box">
                    <div class="index-title">首页视频</div>
                    <div class="index-work index-box">
                        <el-form-item label="首页视频">
                            <el-upload
                                class="avatar-uploader"
                                action="/app/upload/index"
                                :data="{type:1}"
                                :show-file-list="false"
                                :on-success="handleVideo">
                                <video class="index-work-cover avatar" v-if="form.video" :src="form.video" ></video>
                                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                            </el-upload>
                        </el-form-item>
                    </div>
                </div>
                <div class="index-box">
                    <div class="index-title">首页作品背景设置</div>
                    <div class="index-work index-box">
                        <el-form-item :label="item.name" v-for="(item,index) in form.workCover" :key="index">
                            <el-upload
                                class="avatar-uploader"
                                action="/app/upload/index"
                                :data="{type:1,num:index}"
                                :show-file-list="false"
                                :on-success="handleWorkSuccess"
                                :before-upload="beforeAvatarUpload">
                                <img class="index-work-cover avatar" v-if="form.workCover[index].url" :src="form.workCover[index].url" >
                                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                            </el-upload>
                        </el-form-item>
                    </div>
                </div>
                <div class="index-box">
                    <div class="index-title">公司历程设置</div>
                    <el-form-item label="公司历程">
                        <el-upload
                            class="avatar-uploader"
                            action="/app/upload/index"
                            :data="{type:2}"
                            :show-file-list="false"
                            :on-success="handleCourseSuccess"
                            :before-upload="beforeAvatarUpload">
                            <img class="index-course avatar" v-if="form.course" :src="form.course" >
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                    </el-form-item>
                </div>
                <div class="index-box">
                    <div class="index-title">合作伙伴logo设置</div>
                    <el-form-item label="LOGO">
                        <el-upload
                            class="avatar-uploader"
                            action="/app/upload/index"
                            :data="{type:3}"
                            :show-file-list="false"
                            :on-success="handleLogoSuccess"
                            :before-upload="beforeAvatarUpload">
                            <el-button size="small" type="primary">点击上传</el-button>
                        </el-upload>
                    </el-form-item>
                    <!-- <div class=""> -->
                        <transition-group tag="div" class="container index-logo-list">
                            <div class="index-logo-item" v-for="(item,index) in form.logos" :key="item"
                                draggable="true"
                                @dragstart="handleDragStart($event, item)"
                                @dragover.prevent="handleDragOver($event, item)"
                                @dragenter="handleDragEnter($event, item)"
                                @dragend="handleDragEnd($event, item)">
                                <a href="javascript:" class="item-delete" @click.stop="handleDelete($event,index)">
                                    <i class="el-icon-close"></i>
                                </a>
                                <img :src="item" alt="">
                            </div>
                        </transition-group>
                    <!-- </div> -->
                </div>
                <el-form-item class="subnit-btn">
                    <el-button type="primary" @click="hanleOnSubmit">提交保存</el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>
<script>
export default {
    name:"basis",
    data(){
        return {
            form:{
                video:"",
                workCover:[
                    {name:"创意短视频",url:"http://localhost:3000/images/web/index/1.png"},
                    {name:"企业产品",url:"http://localhost:3000/images/web/index/2.png"},
                    {name:"影视广告",url:"http://localhost:3000/images/web/index/3.png"},
                    {name:"ABOUT ME",url:"http://localhost:3000/images/web/index/4.png"},
                ],
                course:"",
                logos:[]
            },
            dragging:null
        }
    },
    mounted(){
        this.handleinit();
    },
    methods:{
        // inti 获取设置数据
        handleinit(){
            this.$axios.get("/app/index").then((res)=>{
                if(res.status == 200){
                    this.form = JSON.parse(res.data.data[0].cont);
                }
                
            }).catch((err)=>{

            })
        },
        // 视频上传
        handleVideo(res, file){
            if(res.code == 200){
                this.form.video = res.data[0];
                this.$message.success(res.msg);
            }
        },
        // 封面上传
        handleWorkSuccess(res, file) {
            if(res.code == 200){
                this.form.workCover[res.num].url = res.data[0];
                this.$message.success(res.msg);
            }
        },
        // 历程上传
        handleCourseSuccess(res, file) {
            if(res.code == 200){
                this.form.course = res.data[0];
                this.$message.success(res.msg);
            }
        },
        // logo上传
        handleLogoSuccess(res, file) {
            if(res.code == 200){
                this.form.logos.push(res.data[0])
                this.$message.success(res.msg);
            }
        },
        // 图片拖拽
        handleDragStart(e, item){
            this.dragging = item;
        },
        handleDragOver(e, item){
            e.dataTransfer.dropEffect = 'move'
        },
        handleDragEnter(e, item){
            e.dataTransfer.effectAllowed = "move"//为需要移动的元素设置dragstart事件
            if(item === this.dragging){
                return
            }
            const newItems = [...this.form.logos]
            const src = newItems.indexOf(this.dragging)
            const dst = newItems.indexOf(item)
        
            newItems.splice(dst, 0, ...newItems.splice(src, 1))
        
            this.form.logos = newItems
        },
        handleDragEnd(e, item){
            this.dragging = null;
        },
        handleDelete(e,i){
            this.form.logos.splice(i,1);
            this.$message({
                type: 'success',
                message: "删除成功"
            });
        },
        // 上传前检测
        beforeAvatarUpload(file) {
            const isLt2M = file.size / 1024 / 1024 < 5;
            if (!isLt2M) {
                this.$message.error('上传图片大小不能超过 5MB!');
            }
            return isLt2M;
        },
        hanleOnSubmit(){
            let str = JSON.stringify(this.form);
            this.$axios.post("/app/index",{cont:str}).then((res)=>{
                this.$message({
                    type: 'success',
                    message: res.data.msg
                });
            }).catch((err)=>{
                this.$message({
                    type: 'error',
                    message: "网络错误"
                });
            })
        }
    }
}
</script>
<style>
.avatar-uploader .el-upload{
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}
.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}
.avatar-uploader .el-upload:hover {
    border-color: #409EFF;
}
.avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 360px;
    height: 40px;
    line-height: 46px;
    text-align: center;
}
</style>
<style lang="scss" scoped>
.basis{
    position: relative;
    width: 100%;
    height: 100%;
    background-color: #fff;
    overflow: hidden;
    .base-head {
        display: flex;
        align-items: center;
        padding: 12px;
        margin-bottom: 12px;
        box-shadow: 0 1px 12px rgba(0,0,0,.1);
        p{
            font-size: 16px;
            width: 120px;
            padding-left: 12px;
        }
    }
    .form {
        height: calc(100% - 58px);
        overflow-y: scroll;
        .index-title {
            vertical-align: middle;
            font-size: 14px;
            line-height: 40px;
            padding: 0 12px 0 0;
            -webkit-box-sizing: border-box;
            box-sizing: border-box;
            padding-bottom: 12px;
            color:#000;
        }
        .index-box{
            // padding: 12px 0;
        }
        .index-work {
            display: flex;
            align-items: center;
            justify-content: space-between;
            .index-work-cover{
                display: block;
                height: 80px;
            }
        }
        .index-logo-list {
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            width: 100%;
            .index-logo-item {
                position: relative;
                margin: 6px;
                width: 120px;
                height: 120px;
                display: flex;
                align-items: center;
                transition: all linear 300ms;
                box-shadow: 0 0 5px rgba(0,0,0,.3);
                .item-delete {
                    position:absolute;
                    top: 0;
                    right: 0;
                    width: 30px;
                    height: 30px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    color: red;
                    opacity: .3;
                    transition-duration: 600ms;
                    i {
                        font-size: 20px;
                    }
                    &:hover{
                        opacity: 1;
                    }
                }
                img{
                    pointer-events: none;
                    width: 100%;
                }
            }
        }
        .subnit-btn {
            padding-top: 20px;
        }
    }
}
</style>




