<template>
    <div class="banner">
        <div class="form">
            <el-form ref="form" :model="form" label-width="120px">
                <el-form-item label="案例列表">
                    <el-upload
                        class="avatar-uploader"
                        action="/app/upload/banner"
                        :data="{type:'work'}"
                        :show-file-list="false"
                        :on-success="handleAvatarSuccess"
                        :before-upload="beforeAvatarUpload">
                        <img v-if="form.work" :src="form.work" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                </el-form-item>
                <el-form-item label="新闻列表">
                    <el-upload
                        class="avatar-uploader"
                        action="/app/upload/banner"
                        :data="{type:'news'}"
                        :show-file-list="false"
                        :on-success="handleAvatarSuccess"
                        :before-upload="beforeAvatarUpload">
                        <img v-if="form.news" :src="form.news" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                </el-form-item>
                <el-form-item label="关于我们">
                    <el-upload
                        class="avatar-uploader"
                        action="/app/upload/banner"
                        :data="{type:'about'}"
                        :show-file-list="false"
                        :on-success="handleAvatarSuccess"
                        :before-upload="beforeAvatarUpload">
                        <img v-if="form.about" :src="form.about" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                </el-form-item>
                <el-form-item label="我的客户">
                    <el-upload
                        class="avatar-uploader"
                        action="/app/upload/banner"
                        :data="{type:'client'}"
                        :show-file-list="false"
                        :on-success="handleAvatarSuccess"
                        :before-upload="beforeAvatarUpload">
                        <img v-if="form.client" :src="form.client" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                </el-form-item>
                <el-form-item label="加入我们">
                    <el-upload
                        class="avatar-uploader"
                        action="/app/upload/banner"
                        :data="{type:'join'}"
                        :show-file-list="false"
                        :on-success="handleAvatarSuccess"
                        :before-upload="beforeAvatarUpload">
                        <img v-if="form.join" :src="form.join" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                </el-form-item>
                <el-form-item label="联系我们">
                    <el-upload
                        class="avatar-uploader"
                        action="/app/upload/banner"
                        :data="{type:'call'}"
                        :show-file-list="false"
                        :on-success="handleAvatarSuccess"
                        :before-upload="beforeAvatarUpload">
                        <img v-if="form.call" :src="form.call" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="hanleOnSubmit">提交保存</el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>
<script>
export default {
    name:"banner",
    data(){
        return {
            form:{
                work:"",
                news:"",
                about:"",
                client:"",
                join:"",
                call:""
            }
        }
    },
    mounted(){
        this.handleInit()
    },
    methods:{
        handleInit(){
            this.$axios.get("/app/get/banner").then((res)=>{
                if(res.status == 200){
                    this.form = JSON.parse(res.data.data[0].cont);
                }
                
            }).catch((err)=>{

            })
        },
        handleAvatarSuccess(res, file){
             if(res.code == 200){
                this.form[res.type] = 'http://localhost:3000'+res.data[0]
                this.$message.success(res.msg);
            }
        },
        beforeAvatarUpload(file){
            const isLt2M = file.size / 1024 / 1024 < 5;
            if (!isLt2M) {
                this.$message.error('上传图片大小不能超过 5MB!');
            }
            return isLt2M;
        },
        hanleOnSubmit(){
            let str = JSON.stringify(this.form);
            this.$axios.post("/app/set/banner",{cont:str}).then((res)=>{
                this.$message({
                    type: 'success',
                    message: res.data.msg
                });
            }).catch((err)=>{
                this.$message({
                    type: 'error',
                    message: "网络错误"
                });
            })
        }
    }
}
</script>
<style lang="scss" scoped>
.el-upload{
    border: 1px dashed #d9d9d9 !important;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}
.avatar-uploader .el-upload:hover {
    border-color: #409EFF;
}
.avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 360px;
    height: 40px;
    line-height: 46px;
    text-align: center;
}
.banner {
    height: 100%;
    overflow-y: scroll;
    .avatar {
        height: 100px;
    }
}
</style>




